import React, { useState, useCallback, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom";
import clsx from "clsx";
import PropTypes from "prop-types";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  Box,
  Button,
  Card,
  Checkbox,
  InputAdornment,
  FormControlLabel,
  IconButton,
  Link,
  SvgIcon,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  makeStyles,
  Container,
} from "@material-ui/core";
import {
  Image as ImageIcon,
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon,
} from "react-feather";
import Label from "src/components/Label";
import moment from "moment";
import {
  getProductTypeRequest,
  deleteProductTypeRequest,
} from "src/actions/productTypeActions";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
  root: {},
  bulkOperations: {
    position: "relative",
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: "absolute",
    width: "100%",
    zIndex: 2,
    backgroundColor: theme.palette.background.default,
  },
  bulkAction: {
    marginLeft: theme.spacing(2),
  },
  queryField: {
    width: 500,
  },
  categoryField: {
    flexBasis: 200,
  },
  availabilityField: {
    marginLeft: theme.spacing(2),
    flexBasis: 200,
  },
  stockField: {
    marginLeft: theme.spacing(2),
  },
  shippableField: {
    marginLeft: theme.spacing(2),
  },
  imageCell: {
    fontSize: 0,
    width: 68,
    flexBasis: 68,
    flexGrow: 0,
    flexShrink: 0,
  },
  image: {
    height: 68,
    width: 68,
  },
}));

function Results({ className, products, ...rest }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const [query, setQuery] = useState("");
  const [itemClick, setItemClick] = useState("");
  const [open, setOpen] = React.useState(false);
  const handleEdit = (item) => {
    console.log("item", item);
    history.push({
      pathname: "/app/management/productType/edit/" + item._id,
      state: item,
    });
  };
  const { enqueueSnackbar } = useSnackbar();
  const [listJob, setListJob] = useState([]);
  const getJobs = useCallback(() => {
    dispatch(
      getProductTypeRequest(
        (res) => {
          console.log("getProjects", res);
          setListJob(res);
        },
        (error) => {
          console.log("error", error);
        }
      )
    );
  }, []);
  useEffect(() => {
    getJobs();
  }, []);

  const onSubmitSuccess = () => {
    enqueueSnackbar("Xoá thành công", {
      variant: "success",
    });
    getJobs();
  };
  const onSubmitFailed = () => {
    enqueueSnackbar("Xoá thất bại", {
      variant: "error",
    });
  };
  const handleDeleteItem = async () => {
    setOpen(false);
    await dispatch(
      deleteProductTypeRequest(
        { id: itemClick._id },
        (res) => {
          onSubmitSuccess();
        },
        (error) => {
          onSubmitFailed();
        }
      )
    );
  };

  const handleOpen = (item) => {
    setItemClick(item);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Container>
        <PerfectScrollbar>
          <Box>
            <Table style={{ marginTop: 56 }}>
              <TableHead>
                <TableRow>
                  <td
                    className="col-md-1  "
                    style={{ paddingLeft: 10, textAlign: "left" }}
                  >
                    STT
                  </td>
                  <td className="col-md-4  " style={{ textAlign: "center" }}>
                    Tên (Vi)
                  </td>
                  <td className="col-md-4  " style={{ textAlign: "center" }}>
                    Tên (En)
                  </td>
                  <td
                    className="col-md-1  "
                    style={{ paddingRight: 10, textAlign: "right" }}
                  ></td>
                </TableRow>
              </TableHead>
              <TableBody>
                {listJob.map((item, index) => {
                  return (
                    <TableRow
                      className="table-row "
                      key={item.id}
                      onClick={() => {}}
                      // selected={selectedOrders.indexOf(order.id) !== -1}
                    >
                      <td style={{ textAlign: "left", paddingLeft: 10 }}>
                        {index + 1}
                      </td>

                      <td>
                        <div>{item.nameVi}</div>
                      </td>
                      <td>
                        <div>{item.nameEn}</div>
                      </td>

                      <td className="row-ct">
                        <button
                          type="button"
                          class="btn btn-primary"
                          style={{ marginRight: 20 }}
                          onClick={() => {
                            handleEdit(item);
                          }}
                        >
                          Sửa
                        </button>
                        <button
                          type="button"
                          class="btn btn-danger"
                          onClick={() => {
                            handleOpen(item);
                          }}
                        >
                          Xoá
                        </button>
                      </td>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </Box>
        </PerfectScrollbar>
      </Container>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <div class="modal-dialog modal-sm" role="document">
          <div class="modal-content">
            <div class="modal-header" style={{ display: "flex" }}>
              <h5
                class="modal-title"
                id="exampleModalLabel"
                style={{ flex: 1 }}
              >
                Thông báo
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
                onClick={handleClose}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">Xác nhận xoá</div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                onClick={handleClose}
                data-dismiss="modal"
              >
                Đóng
              </button>
              <button
                type="button"
                class="btn btn-primary"
                onClick={handleDeleteItem}
              >
                Xác nhận
              </button>
            </div>
          </div>
        </div>
      </Modal>
    </Card>
  );
}

Results.propTypes = {
  className: PropTypes.string,
  products: PropTypes.array,
};

Results.defaultProps = {
  products: [],
};

export default Results;
